import Vue from "vue";
import VueI18n from "vue-i18n";
import VuiZhCN from "vui/locale/lang/zh-CN";
import VuiEnUS from "vui/locale/lang/en-US";
// import VuiZhCN from "vui-design/dist/locale/zh-CN";
// import VuiEnUS from "vui-design/dist/locale/en-US";
import LocaleZhCN from "./lang/zh-CN";
import LocaleEnUS from "./lang/en-US";
import getDefaultLanguage from "@/utils/getDefaultLanguage";

Vue.use(VueI18n);

const language = getDefaultLanguage();
const messages = {
	"zh-CN": Object.assign(VuiZhCN, LocaleZhCN),
	"en-US": Object.assign(VuiEnUS, LocaleEnUS)
};

Vue.config.language = language;

const i18n = new VueI18n({
	locale: language,
	messages
});

export default i18n;


