import VuiIcon from "vui/components/icon";
import MixinLink from "vui/mixins/link";
import guid from "vui/utils/guid";
import getClassNamePrefix from "vui/utils/getClassNamePrefix";
import guardLinkEvent from "vui/utils/guardLinkEvent";

const VuiDropdownMenuItem = {
	name: "vui-dropdown-menu-item",

	inject: {
		vuiDropdown: {
			default: undefined
		},
		vuiDropdownMenu: {
			default: undefined
		},
		vuiDropdownSubmenu: {
			default: undefined
		}
	},

	components: {
		VuiIcon
	},

	mixins: [
		MixinLink
	],

	props: {
		classNamePrefix: {
			type: String,
			default: undefined
		},
		name: {
			type: [String, Number],
			default: () => guid()
		},
		icon: {
			type: String,
			default: undefined
		},
		title: {
			type: String,
			default: undefined
		},
		disabled: {
			type: Boolean,
			default: false
		}
	},

	methods: {
		handleClick(e) {
			const { vuiDropdown, vuiDropdownMenu, vuiDropdownSubmenu, $router: router, name, disabled, href, to, replace, getNextRoute } = this;

			if (disabled) {
				return e.preventDefault();
			}

			vuiDropdownMenu.$emit("click", name);

			if (vuiDropdownSubmenu) {
				vuiDropdownSubmenu.close("menuItemClick", true);
			}
			else if (vuiDropdown) {
				vuiDropdown.close("menuItemClick");
			}

			if (href) {

			}
			else if (to && guardLinkEvent(e)) {
				try {
					let route = getNextRoute();
					let next = replace ? router.replace : router.push;

					next.call(router, route.location).catch(error => undefined);
				}
				catch(error) {
					console.error(error);
				}
			}
		}
	},

	render(h) {
		const { $slots: slots, classNamePrefix: customizedClassNamePrefix, name, disabled, href, to, target, getNextRoute } = this;
		const { handleClick } = this;

		let icon;

		if (slots.icon) {
			icon = slots.icon;
		}
		else if (this.icon) {
			icon = (
				<VuiIcon type={this.icon} />
			);
		}

		let title = slots.title || this.title;

		const classNamePrefix = getClassNamePrefix(customizedClassNamePrefix, "dropdown-menu-item");
		let classes = {};

		classes.el = {
			[`${classNamePrefix}`]: true,
			[`${classNamePrefix}-disabled`]: disabled
		};
		classes.elIcon = `${classNamePrefix}-icon`;
		classes.elTitle = `${classNamePrefix}-title`;

		let props = {
			attrs: {

			},
			class: classes.el,
			on: {
				click: handleClick
			}
		};
		let children = [];

		if (icon) {
			children.push(
				<div class={classes.elIcon}>{icon}</div>
			);
		}

		if (title) {
			children.push(
				<div class={classes.elTitle}>{title}</div>
			);
		}

		if (href || to) {
			props.attrs.href = href || getNextRoute().href;
			props.attrs.target = target;

			return (
				<a {...props}>{children}</a>
			);
		}
		else {
			return (
				<div {...props}>{children}</div>
			);
		}
	}
};

export default VuiDropdownMenuItem;


