import getClassNamePrefix from "vui/utils/getClassNamePrefix";

const VuiMenuDivider = {
	name: "vui-menu-divider",

	props: {
		classNamePrefix: {
			type: String,
			default: undefined
		},
		margin: {
			type: [String, Number],
			default: undefined
		}
	},

	render() {
		let { $slots: slots, classNamePrefix: customizedClassNamePrefix, margin } = this;

		// Class
		let classNamePrefix = getClassNamePrefix(customizedClassNamePrefix, "menu-divider");

		// Style
		let style = {};

		if (margin !== undefined) {
			style.marginTop = `${margin}px`;
			style.marginBottom = `${margin}px`;
		}

		// Render
		return (
			<div class={`${classNamePrefix}`} style={style}></div>
		);
	}
};

export default VuiMenuDivider;


