import Vue from "vue";
import Vui from "vui";
// import Vui from "vui-design";
import Router from "vue-router";
import title from "@/config/title";
import routes from "./routes";

Vue.use(Router);

const router =  new Router({
	mode: "hash",
	scrollBehavior: () => ({ y: 0 }),
	routes
});

router.beforeEach((to, from, next) => {
	Vui.Loading.start();
	next();
});

router.afterEach((to, from) => {
	if (to.meta && to.meta.title) {
		window.document.title = to.meta.title.toLowerCase().replace(/( |^)[a-z]/g, (L) => L.toUpperCase()) + " - VUI";
	}
	else {
		window.document.title = title;
	}

	Vui.Loading.finish();
});

export default router;


