import getClassNamePrefix from "vui/utils/getClassNamePrefix";

const VuiDropdownMenuItemGroup = {
	name: "vui-dropdown-menu-item-group",

	props: {
		classNamePrefix: {
			type: String,
			default: undefined
		},
		title: {
			type: String,
			default: undefined
		}
	},

	render(h) {
		let { vuiMenu, $slots: slots, classNamePrefix: customizedClassNamePrefix, title, indent } = this;

		// Class
		let classNamePrefix = getClassNamePrefix(customizedClassNamePrefix, "dropdown-menu-item-group");

		// Render
		return (
			<div class={`${classNamePrefix}`}>
				<div class={`${classNamePrefix}-header`}>{slots.title || title}</div>
				<div class={`${classNamePrefix}-body`}>{slots.default}</div>
			</div>
		);
	}
};

export default VuiDropdownMenuItemGroup;


